html {
  box-sizing: border-box;
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", "Tahoma", sans-serif;
  font-size: 17px;
  line-height: 28px;
  font-weight: normal;
  font-style: normal;
  color: #212327;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  height: 100%;
  background-image: url("../img/bcg-block1.webp");
  background-repeat: no-repeat;
  background-size: 100% 900px;
  background-color: #1B2A32; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.hidden {
  display: none; }

